
import { Base64 } from './base64.js'
export default function(obj){
	// baseurl config 
	const  { baseURL } = obj;  // mingjs
	
	// vueuse
	return function ( vue ){
		
		// vuerequestthis.request
		vue.prototype.request = async function(params){
			const { url ,data , method  } = params 
			let header = {'Content-Type': 'application/x-www-form-urlencoded'}
			let userInfo = uni.getStorageSync('myinfo');
			let user_id = '';
			if(uni.getStorageSync('loginType')){ //wuye 
				userInfo = uni.getStorageSync('wymyinfo');
				if(userInfo){
					user_id= userInfo.client.id;
				}
				
			}
			if (userInfo) {
				let access_token = userInfo.access_token;
				let app_key = userInfo.client.app_key;
				if(user_id==''){
					user_id = userInfo.client.user_id;
				}
				header = {
					'Content-Type': 'application/x-www-form-urlencoded',
					'authentication': 'USERID '+ Base64.encode(app_key+':'+access_token+':'+user_id)
				}
			}
			
			// uni.showLoading({
			// 	title:''
			// })
			const [ err,res ] = await uni.request({
				url: baseURL + url ,
				header: header,
				data:data,
				method:method
			});
			if (res.code === 408||res.data.code===408) {
				uni.showToast({
					title: '',
					icon: "none"
				})
				setTimeout(() => {
					this.userInfo = ''
					uni.clearStorageSync()
					uni.reLaunch({
						url: '/pages/login/index'
					})
				}, 1000)
			} else if (res.code === 400||res.data.code===400) {
				uni.showToast({
					title: res.data.desc,
					icon: "none"
				})
				if(res.data.desc==''){
					setTimeout(() => {
						uni.clearStorageSync()
						uni.reLaunch({
							url: '/pages/login/index'
						})
					}, 1000)
				}
				this.userInfo = ''
				return 
			}else {
				return res.data;	
			}
		}
	}
}